.report-list-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  margin-top: 100px;
}

.report-table-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.report-table {
  width: 70%;
  border-collapse: collapse;
  border: 1px solid #ccc;
  table-layout: fixed;
  margin-top: 20px;
}

.report-table th,
.report-table td {
  padding: 12px;
  border: 1px solid #ccc;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.report-table th {
  background-color: #f2f2f2;
}

.report-table td:first-child {
  width: 60%;
}
