body{
  padding: 0;
  margin: 0;
}

.burgerBar{
  width: 40px;
  height: 40px;
  background-image: url('../img/burger-bar.png');
  background-repeat: no-repeat;
  background-size: 22px 22px;
  background-position: center; /* 이미지를 중앙에 배치 */
  margin-left: 10px;
  margin-right: 10px;
  cursor: pointer;
  
}

