.nav-search-input{
  width: 400px;
  height: 40px;
  background-color: white;
  font-size: 17px;
  margin: 0 auto;
  border: none;
  outline: none;
  border-radius: 3px;
  border: solid gray 1px;
  padding-right: 10px;
  padding-left: 32px;
  background-image: url(../img/search.png);
  background-repeat: no-repeat;
  background-size: 20px;
  background-position: 8px center;
  position: relative;
  z-index: 9;
}

