.bg-container{
  width: 270px;
  height: 650px;
  border: solid;
  position: absolute;
  position: fixed;
  z-index: 10;
  top: 70px;
  background-color: white;
  display: block;
  transition: 0.2s;
  transform: translateX(0);
}

.bg-container-none{
width: 270px;
height: 650px;
border: solid;
position: absolute;
position: fixed;
z-index: 10;
top: 70px;
background-color: white;
display: block;
transform: translateX(-500px);
transition: 0.5s;
}
.bg-container-none div{
  display: none;
}
