.container{
    display: none;
  }
  
  .p{
    display: inline;
  }
  .p1{
    display: none;
  }
  .container2{
    display: inline;
  }
  
  .post2-container{
    overflow: hidden;
    position: relative;
    margin: 0 auto;
    width: 95%;
    max-height: 1;
    margin-top: 20px;
  }
  
  .post2{
    width: 200px;
    height: 150px;
    border: solid;
    display: inline-block;
    margin: 17px;
  }
  
  .my-container1{
    margin: 0 auto;
    margin-top: 50px;
    width: 95%;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .rcm-bUtton{
    width: 30px;
    height: 30px;
    margin-right: 10px;
    cursor: pointer;
  }
  
  .nmp{
    margin: 0 auto;
    margin-top: 10px;
    display: flex;
    width: 95%;
    height: 54.2px;
  }
  
  .el{
    width: 545px;
    height: 38px;
    padding: 8px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid black;
  }
  
  .mb,
  .uh{
    width: 272px;
    height: 38px;
    padding: 8px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid black;
  }
  
  .pj{
    width: 290px;
    height: 38px;
    padding: 8px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid black;
  }
  
  .postCheck{
    border-bottom: 1px solid gray;
    width: 95%;
    height: 80px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    text-decoration: none;
  }
  
  .post-el{
    width: 545px;
    height: 38px;
    padding: 8px;
    display: flex;
    align-items: center;
    text-decoration: none;
    cursor: pointer;
  }
  
  .post-mb,
  .post-uh,
  .post-pj{
    width: 272px;
    height: 38px;
    padding: 8px;
    display: flex;
    align-items: center;
    font-size: 14px
  }
  .post-img{
    width: 50px;
    height: 50px;
    border-radius: 10px;
    border: solid;
    margin-left: 1%;
  }
  
  .post-text{
    width: 80%;
    color: black;
    margin-left: 9px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
  
  a{
    text-decoration: none;  
  }
  
  .myboardTitle{
    width: 100%;
    height: 30px;
    border-top: 1px black solid;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
  
  

  
  
  @media ( max-width: 400px ) {
    .rcm-text1{
        font-size: 18px;
        margin-left: 10px;
    }
}
