* {
  scrollbar-width: thin; /* Firefox에 대한 스크롤바 너비 설정 */
  -ms-overflow-style: none; /* IE/Edge에 대한 스크롤바 스타일 설정 */
}

*::-webkit-scrollbar {
  width: 5px; /* Chrome, Safari, Opera에 대한 스크롤바 너비 설정 */
  height: 5px;
}

*::-webkit-scrollbar-thumb {
  background-color: #c0c0c0; /* 스크롤바 색상 설정 */
}


*::-webkit-scrollbar-track {
  background-color: transparent; /* 스크롤바 트랙 색상 설정 */
}


.rcm-container{
  margin: 0 auto;
  margin-top: 20px;
  width: 95%;
  height: 30px;
  display: flex;
}

.rcm-container1{
  margin: 0 auto;
  margin-top: 50px;
  width: 95%;
  height: 30px;
  display: flex;
}

.rcm-text{
  font-size: 20px;
}

.rcm-text1{
  font-size: 20px;
}

.slide-img{
  width: 200px;
  height: 100px;
  padding: 20px;
  background-size: cover;
  border: none;
  border-radius: 5px;
  background-repeat: no-repeat;
  background-position: center;
}

.post{
  width: 200px;
  height: 100px;
  background-color: #eeeeee88;
  border-radius: 10px;
  margin-right: 60px;
}

.slider-container {
  overflow: hidden;
  position: relative;
  margin: 0 auto;
  width: 95%;
  height: 200px;
  display: flex;
  align-items: center;
}

.slider {
  display: flex;
  transition: transform 0.5s ease-in-out;

}

.slide {
  width: 200px;
  height: 80px;
  margin-bottom: 1px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.133);
  text-align: center; 
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.slide-text{
  width: 240px;
  margin-top: 10px;
  font-size: 17px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.prev-button{
  width: 40px;
  height: 40px;
  position: absolute;
  left: 0px;
  margin-bottom: 30px;
  border-radius: 100px;
  background-image: url(../img/l.png);
  background-repeat: no-repeat;
  background-position: 13px;
  background-size: 30%;
  background-color: white;
  border: solid gray 1px;
  cursor: pointer;
}

.next-button {
  width: 40px;
  height: 40px;
  position: absolute;
  right: 0px;
  margin-bottom: 30px;
  border-radius: 100px;
  background-color: white;
  border: solid gray 1px;
  background-image: url(../img/r.png);
  background-repeat: no-repeat;
  background-position: 14px;
  background-size: 30%;
  cursor: pointer;
}


