@media ( max-width: 900px ) {

.rcm-container{
  margin-top: 100px;
}
.rcm-bUtton{
  display: none;
}
.post2-container{
  display: none;
}
#container1{
  display: block;
}
}



.post-list-container {
  display: flex;
  flex-direction: column;
}
.post-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 25px;
}
.post-item {
  margin-right: 10px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 4px 1.5px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  text-overflow: ellipsis;
  transition: transform 0.3s ease;
  cursor: pointer;
}
.post-item:hover { transform: translateY(-4px); }
.post-item a { text-decoration: none; color: #333; }

.post-thumbnail {
  position: relative;
  height: 180px;
  overflow: hidden;
}
.post-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.post-content { padding: 16px; }
.post-title {
  max-width: 240px;
  font-size: 20px;
  margin: 0;  
  margin-bottom: 8px;
  text-overflow: ellipsis;
  white-space: nowrap; 
  overflow: hidden;

}
.post-description {
  font-size: 16px;
  margin: 0;
  margin-bottom: 12px;
  color: #777;
}
.post-meta {
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #999;
}
.post-meta .post-date::before{
  content: '-';
  margin: 0 6px;
}
