/* editorStyles.css */

/* 코드 블록 스타일 */
.sun-editor .sun-editor-editable pre {
  background-color: #303030; /* 코드 블록 배경색 */
  color: #bcbcbc; /* 코드 블록 텍스트 색상 */
  padding: 10px;
  font-family: 'Courier New', Courier, monospace;
  font-size: 14px;
  line-height: 1.5;
  overflow-x: auto;
  white-space: pre-wrap;  
  word-break: break-all;
}

pre {
  background-color: #303030; /* 코드 블록 배경색 */
  color: #bcbcbc; /* 코드 블록 텍스트 색상 */
  padding: 10px;
  font-family: 'Courier New', Courier, monospace;
  font-size: 14px;
  line-height: 1.5;
  overflow-x: auto;
  white-space: pre-wrap;  
  word-break: break-all;
}
/* 코드 블록 라인 넘버 스타일 */
.sun-editor .sun-editor-editable pre .line-number {
  display: inline-block;
  width: 30px;
  padding-right: 10px;
  color: #999; /* 라인 넘버 색상 */
  text-align: right;
  user-select: none;
}
