.search-container {
    margin: 0 auto;
    margin-top: 105px;
    width: 95%;
    height: 250px;
    border-radius: 10px;
    position: relative;
    background: rgb(2, 0, 36);
    background: linear-gradient(
      90deg,
      rgba(2, 0, 36, 1) 0%,
      rgba(0, 245, 255, 1) 0%,
      rgba(136, 0, 255, 1) 74%
    );
    background-size: cover;
  }
  
  .select-cont{
    width: 85%;
    height: 40px;
    background-color: #eee;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px 0 10px;
  }

  .text {
    margin-top: 22px;
    width: 670px;
    height: 50px;
    font-size: 30px;
    color: #eee;
    text-align: center;
  }
  
  .input-container {
    display: flex;
    justify-content: center;
    margin-top: 10px;
  }
  
  .input-container1 {
    margin-top: 10px;
  }
  
  .search-input {
    width: 700px;
    height: 40px;
    background-color: white;
    font-size: 17px;
    margin: 0 auto;
    border: none;
    outline: none;
    border-radius: 3px;
    padding-right: 10px;
    padding-left: 32px;
    background-image: url(../img/search.png);
    background-repeat: no-repeat;
    background-size: 20px;
    background-position: 7px center;
    display: block;
    margin: 0 auto;
  }
  
  .search-table {
    width: 700px;
    height: 100%;
    margin: 0 auto;
    display: none;
    position: absolute;
    top: calc(100% + 10px);
    left: 50%;
    top: 49%;
    transform: translateX(-50%);
    background-color: #fff;
    z-index: 1;
    padding-top: 20px;
    overflow: hidden;
    border-top: solid black 0.5;
    border: solid 0.5px gray;
  }

  .search-table.visible {
    display: block;
  }
  
  .select{
    width: 100%;
    height: 40px;
    display: flex;
    align-items: center;
    padding-left: 30px;
    cursor: pointer;
  }

  .select1{
    width: 100%;
    height: 40px;
    display: flex;
    align-items: center;
    padding-left: 30px;
    cursor: pointer;
  }

  .select:hover{
    background-color: rgb(214, 214, 214);
  }
  
  .select-button{
    background-color: white;
    width: 50%;
    height: 80%;
    border: solid gray 0.5px;
    border-radius: 5px;
    font-weight: 550;
    font-size: 17px;
    cursor: pointer;
  }

  @media (max-width: 900px) {
    .search-container {
      display: none;
    }
  }