.inquiry-list-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  margin-top: 100px;
}

.inquiry-table {
  border-collapse: collapse;
  width: 400px;
}

.inquiry-table thead {
  background-color: #f2f2f2;
}

.inquiry-table th {
  border: 1px solid #ddd;
  padding: 8px;
}

.inquiry-table td {
  border: 1px solid #ddd;
  padding: 8px;
}

.inquiry-table td a {
  text-decoration: none;
  color: #333;
}

