.newNav {
  border-bottom: 1px solid;
  width: 100%;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 75px;
  background-color: white;
  z-index: 5;
}

.navLinks {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.navLinks li {
  margin-right: 90px;
}

.navLink {
  color: #333;
  text-decoration: none;
}

.logout {
  margin-right: 25px;
  width: 120px;
  height: 40px;
  font-size: 15px;
  text-align: center;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(163, 98, 225);
  color: #eee;
  cursor: pointer;
}

.logout:hover {
  background-color: rgb(138, 78, 191);
}

.logout:active {
  background-color: rgb(105, 58, 142);
}
