.boardwrite{
  margin-top: 70px;
}
.tagIputGroup{ 
    position:relative; 
    margin-top:25px;
    margin-bottom:40px; 
}

#label {
  font-weight: bold;
  font-size: 15px;
}
#select {
  font-family: Arial, sans-serif;
  font-size: 16px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #f7f7f7;
  color: #555;
  appearance: none;
  outline: none;
  cursor: pointer;
  transition: border-color 0.3s ease;
}

#select:hover { border-color: #555; }

#select option {
  font-family: Arial, sans-serif;
  font-size: 14px;
  background-color: #f7f7f7;
  color: #555;
}
#select option:hover { background-color: #eee; }

#select option[value=""] {
  font-style: italic;
  color: #999;
}

#tags{
    font-size:18px;
    padding:10px 10px 10px 5px;
    display:block;
    width:350px;
    border:none;
    border-bottom:1px solid #757575;
}
#tags:focus{ outline:none; }

.label{
    color:#999; 
    font-size:18px;
    font-weight:normal;
    position:absolute;
    pointer-events:none;
    left:5px;
    top:10px;
    transition:0.3s ease all; 
    -moz-transition:0.3s ease all; 
    -webkit-transition:0.3s ease all;
}

#tags:focus ~ .label, #tags:valid ~ .label {
    top:-20px;
    font-size:14px;
    color:#7d1ac9;
}

.bar{ position:relative; display:block; width:300px; }
.bar:before, .bar:after {   
    content:'';
    height:2px; 
    width:0;
    top:0;
    bottom:1px; 
    position:absolute;
    background:#7d1ac9; 
    transition:0.2s ease all; 
    -moz-transition:0.2s ease all; 
    -webkit-transition:0.2s ease all;
}
.bar::before { left:60.5%; }
.bar::after { right: 38.5%; }

#tags:focus ~ .bar:before, #tags:focus ~ .bar:after {
    width:61%;
}

.highlight {
    position:absolute;
    height:60%; 
    width:100px; 
    top:25%; 
    left:0;
    pointer-events:none;
    opacity:0.5;
}

#tags:focus ~ .highlight {
    -webkit-animation:inputHighlighter 0.4s ease;
    -moz-animation:inputHighlighter 0.4s ease;
    animation:inputHighlighter 0.4s ease;
}

@-webkit-keyframes inputHighlighter { from{ background:#7d1ac9; } to{ width:0; background:transparent; } }
@-moz-keyframes inputHighlighter { from{ background:#7d1ac9; } to{ width:0; background:transparent; } }
@keyframes inputHighlighter { from{ background:#7d1ac9; } to{ width:0; background:transparent; }}

.wtireContainer {
    max-width: 800px;
    margin: 0 auto;
    padding: 5px;
}

.form-group {
    margin-top:10px;
    margin-bottom: 20px;
}

.title-input {
  width: 100%;
  padding: 12px;
  border: none;
  font-size: 30px;
  color: #333;
  outline: none;
  position: relative;
}

.title-input::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #000;
}

.title-input:focus {
  border-bottom: 1px solid black;
}

.title-input:focus::before {
  height: 100%;
}            

.title-input::placeholder {
    color: #999;
}               

.tag-input-container {
    display: flex;
    align-items: center;
    flex-wrap: wrap;    
}

.tag {
    display: flex;
    font-size:16px;
    font-weight:400;
    margin:5px;
    padding:2px 20px 4px 15px;
    background:#f8f9fe;
    color:#a428db;
    border-radius:20px;
    padding: 4px;
    margin-right: 4px;
}
.tag:hover{
    background:#fff;
}
.tag span {
    margin-right: 4px;
}

.remove-tag {
    background-color: transparent;
    border: none;
    color: #888;
    cursor: pointer;
}

.submit-btn {
    margin-left:85%;
    padding: 10px 24px;
    margin-top:-30px;
    border-radius: 4px;
    background-color: #a428db;
    color: #fff;
    font-weight: bold;
    font-size:18px;
    cursor: pointer;
    border: none;
}
.submit-btn:hover {
    background-color: #7d1ac9;
}