.footer-container {
  width: 95%;
  margin: 0 auto;
  padding: 20px;
  margin-top: 50px;
  background-color: black;
}

.h2{
  color: white;
  margin-bottom: 0%;
}


.footer-content {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.footer-section.about {
  flex-basis: 60%;
}

.footer-section.about h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.footer-section.about p {
  color: #888;
  line-height: 1.5;
  margin-bottom: 20px;
}

.contact span {
  display: block;
  margin-bottom: 5px;
}

/* Links Section */
.footer-section.links {
  margin-top: 30px;
  flex-basis: 40%;
}

.footer-a{
  text-decoration: none;
  margin-right: 30px;
  color: #888;
  font-size: 18px;
  margin-right: 20px;
}

.footer-section.links h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.footer-section.links ul {
  list-style-type: none;
  padding: 0;
}

.footer-section.links ul li {
  margin-bottom: 10px;
}

.footer-section.links ul li a {
  color: #888;
  text-decoration: none;
}
.icon-container{
  width: 100%;
  height: 100px;
  border-top: 1px solid #888;
  margin: 0 auto;
}

.sns-container{
  width: 300px;
  height: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.icons{
  size: 20;
  color: white;
}

.sns1{
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  border: solid white 1px;
  width: 50px;
  height: 50px;
}

.footer-bottom {
  text-align: center;
  padding-top: 20px;

}
.footer-bottom2{
  text-align: center;
  padding-top: 20px;
  color:#888;
  font-size: 7px;
}
.footer-bottom p {
  color: #888;
  font-size: 14px;
  margin: 0;
}

@media ( max-width: 900px ) {

  .footer-container{
    display: none;
  }
}