.reply-page-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 110px;
}

.reply-input-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.reply-textarea {
  width: 400px;
  height: 200px;
  resize: none;
  margin-bottom: 10px;
}

.reply-submit-button {
  padding: 10px 20px;
  background-color: silver;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 16px;
}
